import i18n from "i18next";
import { tr } from "./tr";
import { en } from "./en"
import { initReactI18next } from "react-i18next";

// Dil deiimi daha nce yapldysa localStorage n Cache belleinde tutuyor oradan okuyor 
// Dil deiimi daha nce yaplmadysa "navigator.language" bilgisayar dilini alyor ona ulaamaz ise "tr" olarak setleniyor.
const lang = localStorage.getItem("lang") || navigator.language || "tr";

i18n
    .use(initReactI18next)
    .init({
        resources: { tr: tr, en: en },
        fallbackLng: lang,
        ns: ['translation'],
        defaultNS: 'translation',
        keySeparator: ".",
        interpolation: { escapeValue: false, formatSeparator: ',' },
        react: { useSuspense: true }
    });