/*
    Buras Login olan kullancy localStorage n Cache belleinde tutuyor.
 */
export function storeAuthState(auth) {
    localStorage.setItem('auth', JSON.stringify(auth));
}

/*
    Buras Login olan kullancy localStorage n Cache belleinde'den alyor, dolays ile sayfa refresh yapldnda kullanc bilgileri,
    kaybolmam oluyor.
 */

export function loadAuthState() {
    const defaultState = {id: 0};
    const authStateInStorage = localStorage.getItem('auth');

    if (!authStateInStorage) {
        return defaultState;
    }
    try {
        return JSON.parse(authStateInStorage);
    } catch (error) {
        return defaultState;
    }
}

/*
    Buras Login olan kullancnn token bilgilerini localStorage n Cache belleinde tutuyor.
 */
export function storeToken(token) {
    if (token) {
        localStorage.setItem("token", JSON.stringify(token));
    } else {
        localStorage.removeItem("token")
    }
}

/*
    Buras Login olan kullancnn token bilgilerini localStorage n Cache belleinde'den alyor, dolays ile sayfa refresh yapldnda kullanc
    token bilgileri kaybolmam oluyor ve bu sayede yekilendirme ilemlerini kolaylkla yaplmakta.
 */
export function loadToken() {
    const tokenInString = localStorage.getItem("token");
    if (!tokenInString) {
        return null;
    }
    try {
        return JSON.parse(tokenInString);
    } catch (e) {
        return null;
    }
}